.quality {
  background: url("/img/quality-bg.png") no-repeat center;
  background-size: cover;
  padding: 191px 0 295px;
  background-position: 0 -5px;
}

.quality__title {
  margin-bottom: 23px;
}

.quality__content {
  display: flex;
  max-width: 1000px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-top: -75px;
}

.quality__content-item:first-child {
  flex: 0 1 558px;
}

.quality__content-item:last-child {
  flex: 0 1 230px;
  margin-top: 80px;
}

.quality__text {
  font-size: 20px;
  font-weight: 400;
  line-height: 22px;
  text-align: left;
}

.quality__text:not(.quality__text_bold) {
  margin-bottom: 22px;
}

.quality__text_bold {
  font-family: "Intro Bold";
  font-size: 22px;
  font-weight: 700;
  line-height: 24.2px;
  margin-top: 31px;
  max-width: 390px;
  width: 100%;
  margin-bottom: 31px;
}

.quality__subtitle {
  font-family: "Intro Bold";
  font-size: 45px;
  font-weight: 700;
  line-height: 49px;
  color: #E31A4D;
}

.quality__descr {
  font-size: 16px;
  font-weight: 400;
  line-height: 17.6px;
  margin-top: 10px;
}

.quality__descr:not(:last-child) {
  margin-bottom: 15px;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .quality {
    background: url("/img/quality-bg@2x.png") no-repeat center;
    background-size: cover;
    background-position: 0 -5px;
  }
}